<template>
    <p-data-table
        :value="testResults"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm"
        :loading="loading"
        v-model:selection="selectedBloodTest"
        selectionMode="single"
        dataKey="testOrderId"
    >
        <p-column header="ID" field="testOrderId" />
        <p-column header="Order Date" field="bloodTestOrderDate" />
        <p-column header="E2" field="etwo">
            <template #body="{data}">
                {{ data.etwoStat == "Result" ? data.etwo : data.etwoStat }}
            </template>
        </p-column>
        <p-column header="LH" field="lh">
            <template #body="{data}">
                {{ data.lhStat == "Result" ? data.lh : data.lhStat }}
            </template>
        </p-column>
        <p-column header="PROG" field="prog">
            <template #body="{data}">
                {{ data.progStat == "Result" ? data.prog : data.progStat }}
            </template>
        </p-column>
        <p-column header="FSH" field="fsh">
            <template #body="{data}">
                {{ data.fshStat == "Result" ? data.fsh : data.fshStat }}
            </template>
        </p-column>
        <p-column header="TSH" field="tsh">
            <template #body="{data}">
                {{ data.tshStat == "Result" ? data.tsh : data.tshStat }}
            </template>
        </p-column>
        <p-column header="PRL" field="prl">
            <template #body="{data}">
                {{ data.prlStat == "Result" ? data.prl : data.prlStat }}
            </template>
        </p-column>
        <p-column header="BhCG" field="bhcg">
            <template #body="{data}">
                {{ data.bhcgStat == "Result" ? data.bhcg : data.bhcgStat }}
            </template>
        </p-column>
        <p-column header="TESTO" field="testo">
            <template #body="{data}">
                {{ data.testoStat == "Result" ? data.testo : data.testoStat }}
            </template>
        </p-column>
        <p-column header="DHEAS" field="dheas">
            <template #body="{data}">
                {{ data.dheasStat == "Result" ? data.dheas : data.dheasStat }}
            </template>
        </p-column>
        <p-column header="AMHP" field="amh">
            <template #body="{data}">
                {{ data.amhStat == "Result" ? data.amh : data.amhStat }}
            </template>
        </p-column>
        <p-column header="" style="width: 50px">
            <template #body="{data}">
                <p-button
                    label="Barcode"
                    class="p-button-sm"
                    @click="showBarcode(data.testOrderId)"
                />
            </template>
        </p-column>

        <template #footer>
            <p-button label="Order Test" class="p-mr-2" @click="add" />
            <p-button label="Edit" class="p-mr-2" @click="edit" />
        </template>
    </p-data-table>
</template>

<script>
import { useBloodTestsReportInit } from "@/composable/blood-tests";
import { onMounted, onUnmounted } from "vue";
import { useToast } from "primevue/usetoast";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const {
            testResults,
            loading,
            getBloodTestResults,
            orderBloodTestConfirmDialog,
            editDailogVisible,
            selectedBloodTest,
            showBarcode
        } = useBloodTestsReportInit();

        const toast = useToast();

        onMounted(() => {
            getBloodTestResults();

            emitter.on(
                "blood-test-edit-dailog:reload-test-results",
                getBloodTestResults
            );
        });

        onUnmounted(() => {
            emitter.off(
                "blood-test-edit-dailog:reload-test-results",
                getBloodTestResults
            );
        });

        return {
            testResults,
            selectedBloodTest,
            loading,
            showBarcode,
            add: () => {
                orderBloodTestConfirmDialog.value = true;
            },
            edit: () => {
                if (selectedBloodTest.value == null) {
                    toast.add({
                        severity: "warn",
                        summary: "Select Row",
                        detail: "Please select a row first",
                        life: 3000
                    });
                    return;
                }
                editDailogVisible.value = true;
            }
        };
    }
};
</script>
